<template lang="html">
  <v-card flat light>
    <v-form>
      <v-container grid-list-md>
        <strong>Case Basics</strong>
        <v-layout row wrap>
          <v-flex v-for="e in caseBasics" :key="e.label" xs4>
            <v-text-field
              :label="e.label"
              v-model="e.value"
            ></v-text-field>
          </v-flex>
        </v-layout>
        <strong>Investigation Period</strong>
        <v-layout row wrap>
          <v-flex v-for="e in invPeriod" :key="e.label" xs6>
            <DatePicker v-on:invDate="e.value = $event" :label="e.label"></DatePicker>
          </v-flex>
        </v-layout>
        <v-btn @click="save" color="primary">Save</v-btn>
      </v-container>
    </v-form>
  </v-card>
</template>

<script>
import * as Neo4j from 'neo4j-driver'
import DatePicker from '@/components/DatePicker'
import { mapMutations } from 'vuex'

export default {
  data () {
    return {
      caseBasics: [
        { label: 'Case ID', value: '' },
        { label: 'Investigating Office', value: '' },
        { label: 'Investigator', value: '' }
      ],
      invPeriod: [
        { label: 'Investigation Start', value: '' },
        { label: 'Investigation End', value: '' }
      ]
    }
  },

  async created () {
    const driver = await Neo4j.v1.driver('bolt://localhost:11008', Neo4j.v1.auth.basic('neo4j', 'goldworks55'))
    console.log(driver)
  },

  components: {
    DatePicker
  },

  methods: {
    ...mapMutations([
      'saveCaseInfo'
    ]),
    async save () {
      const info = {
        caseID: this.caseBasics[0].value,
        invOffice: this.caseBasics[1].value,
        whiName: this.caseBasics[2].value,
        invStart: this.invPeriod[0].value,
        invEnd: this.invPeriod[1].value
      }
      this.saveCaseInfo(info)
      const driver = await Neo4j.v1.driver('bolt://hobby-inpafhinbgiagbkekngcldbl.dbs.graphenedb.com:24786', Neo4j.v1.auth.basic('neo4j', 'safePass'))
      const session = driver.session()
      let res = await session.run(
        `CREATE (case:Case { id: '${this.caseBasics[0].value}' })`
      )
      console.log(res)
    }
  }
}
</script>

